<template>
  <div class="supplement-page relative min-w-[1200px]">
    <page-header></page-header>
    <main class="w-[1200px] mx-auto">
      <div class="mt-5 pl-2 text-[#aeb9c0]">首页>在线工具><span class="blue-text">智能选题</span></div>
      <section>
        <h1 class="text-center text-[44px] mt-10 mb-12">析易-回顾性队列研究补证工具</h1>
      </section>
      <section class="shadow-[0_0_14px_4px_rgba(0,0,0,0.1)] pl-4 pr-8 pt-4 pb-10 mb-12 rounded-lg">
        <div class="flex items-center justify-center pl-4 mt-4">
          <div class="mr-2 w-[100px] text-right">段落选择：</div>
          <el-select v-model="paragraphValue" placeholder="请选择段落选择" class="w-[600px]" @change="changeParagraphValue">
            <el-option v-for="item in paragraphOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </div>
        <div class="flex items-center justify-center pl-4 mt-4">
          <div class="mr-2 w-[100px] text-right">模块：</div>
          <el-select v-model="moduleValue" placeholder="请选择模块" class="w-[600px]" @change="changeModuleValue">
            <el-option v-for="item in moduleOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </div>
        <div class="flex items-center justify-center pl-4 mt-4">
          <div class="mr-2 w-[100px] text-right">修证内容：</div>
          <div class="w-[600px]">
            <el-input v-model="contentValue" :placeholder="contentPlaceholder" maxlength="100" :disabled="fristIndex==3"></el-input>
          </div>
        </div>
        <file-upload class="pl-4 pt-5" title="上传参考文献" ref="profileUploadRef" desc="为保证学习质量，暂不支持图片图表、音视频等最多可传20个;支持扩展名：.pdf"></file-upload>
        <div class="text-center pl-4 mt-8 mb-10">
          <el-button type="primary" size="medium" class="w-[200px]" @click="onSumbit">提交</el-button>
        </div>
      </section>
    </main>
    <section class="w-[1200px] mx-auto my-12 px-9 border border-dashed border-[#2b323d] flex items-center justify-between h-[150px]" v-if="file.name">
      <div class="flex items-center">
        <img class="w-[64px]" src="@/assets/word.png" />
        <div class="flex-1 overflow-hidden ml-3">
          <div class="break-all truncate">{{ file.name }}</div>
          <div class="mt-2 text-xs text-[#999]">{{ bytesToSize(file.size) }}</div>
        </div>
      </div>
      <div>
        <el-button class="w-[150px]">预览</el-button>
        <el-button type="primary" class="w-[150px]" @click="onDownload">下载文件</el-button>
      </div>
    </section>
    <page-footer></page-footer>
  </div>
</template>

<script>
import PageHeader from "@/views/components/PageHeader.vue"
import PageFooter from "@/views/components/PageFooter.vue"
import FileUpload from "@/views/components/FileUpload.vue"
import { bytesToSize } from "@/utils/validate"

export default {
  components: {
    PageHeader,
    PageFooter,
    FileUpload,
  },
  data() {
    return {
      paragraphValue: "",
      moduleValue: "",
      contentValue: "",
      contentPlaceholder: "请输入修证内容",
      paragraphOptions: [
        {
          value: "0",
          label: "引言",
          children: [
            {
              value: "0",
              label: "疾病说明",
              placeholder: "例如：息肉",
            },
            {
              value: "1",
              label: "治疗方法",
              placeholder: "例如：中医治疗结直肠息肉方法",
            },
          ],
        },
        {
          value: "1",
          label: "资料与方法",
          children: [
            {
              value: "0",
              label: "指标说明",
              placeholder: "例如：术后 IL-6 水平（pg/mL）的定义及作用",
            },
            {
              value: "1",
              label: "治疗方法操作流程",
              placeholder: "例如：冷切套息肉治疗术治疗中小型结肠息肉的治疗过程和操作",
            },
          ],
        },
        {
          value: "2",
          label: "讨论",
          children: [
            {
              value: "0",
              label: "疾病机理",
              placeholder: "例如：中小型结肠息肉发病机理",
            },
            {
              value: "1",
              label: "治疗原理",
              placeholder: "例如：冷切套息肉治疗术治疗中小型结肠息肉的原理",
            },
            {
              value: "2",
              label: "指标对比解释",
              placeholder: "例如：冷切套息肉治疗术组的术后出血量小于热切套息肉治疗术组，两组呈现显著性差异",
            },
          ],
        },
        {
          value: "3",
          label: "摘要",
          children: [
            {
              value: "0",
              label: "中英文",
            },
            {
              value: "1",
              label: "中文",
            },
            {
              value: "2",
              label: "英文",
            },
          ],
        },
      ],
      moduleOptions: [],
      file: {
        name: "",
        url: "",
        size: "",
      },
      fristIndex: "",
    }
  },
  methods: {
    bytesToSize(bytes) {
      return bytesToSize(bytes)
    },
    onSumbit() {
      this.file = {
        name: "心外膜消融术临床实际意义是否大于射频消融术",
        url: "https://space-uat.oss-cn-shenzhen.aliyuncs.com/supports/202409/20240906064114391.42816432972654.docx",
        size: "2344",
      }
      // const form = this.$refs.homeFormRef.$refs.form
      // const normalProfileList = this.$refs.normalProfileRef.profileList
      // const medicalProfileList = this.$refs.medicalProfileRef.profileList
      // const normalUploadList = this.$refs.normalUploadRef.list
      // const medicalUploadList = this.$refs.medicalUploadRef.list
      // const documentUploadList = this.$refs.documentUploadRef.list
      // console.log("form", form.model)
      // console.log("normalProfileList", normalProfileList)
      // console.log("medicalProfileList", medicalProfileList)
      // console.log("normalUploadList", normalUploadList)
      // console.log("medicalUploadList", medicalUploadList)
      // console.log("documentUploadList", documentUploadList)
    },
    onDownload() {
      const url = this.file.url
      const fileExtension = this.getFileExtension(url)
      const name = this.file.name + "." + fileExtension
      this.downloadFileFromApi(url, name)
    },
    async downloadFileFromApi(fileUrl, fileName) {
      try {
        const response = await fetch(fileUrl)
        const blob = await response.blob()
        const url = URL.createObjectURL(blob)

        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", fileName)
        document.body.appendChild(link)
        link.click()

        // 清理工作
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
      } catch (error) {
        console.error("下载失败:", error)
      }
    },
    changeParagraphValue(val) {
      this.fristIndex = val
      this.moduleOptions = this.paragraphOptions[val].children
      this.moduleValue = ""
      this.contentValue = ""
      this.contentPlaceholder = "请输入修证内容"
    },
    changeModuleValue(val) {
      this.contentPlaceholder = this.paragraphOptions[this.fristIndex].children[val].placeholder || "请输入修证内容"
    },
  },
  destroyed() {},
}
</script>
<style scoped lang="scss">
.supplement-page {
  background: linear-gradient(to bottom, #ebfafb, #feffff), url("~@/assets/bg.png") no-repeat center top;
}
</style>
<style lang="scss">
.supplement-page {
  .el-form--label-top .el-form-item__label {
    padding-bottom: 0;
  }

  .el-upload-dragger {
    padding-bottom: 20px;
  }

  .el-upload,
  .el-upload-dragger {
    width: 100%;
    height: auto;
  }

  .el-card {
    overflow: visible;
  }
}
</style>
